.users {
    
    margin-right: 0.3rem;
    border-radius: 0.5rem;
    background-color: rgb(184, 189, 187);
}
.users img {
    width: 70px;
}
.preloader {
    text-align: center;
}
.usersContainer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
@media(max-width:800px){
    .usersContainer{
        grid-template-columns: 1fr 1fr ;
    }
}
@media(max-width:400px){
    .usersContainer{
        grid-template-columns: 1fr ;
    }
}